import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import IncomeInput from '../../components/IncomeInput';

const Home = () => {
	const handleClick = (e: MouseEvent) => {
		e.preventDefault()
		route('/expense/basic')
	};

	const handleLoadModel = (e: MouseEvent) => {
		Stores.LoadSavedModel()
		route('/expense/basic')
	};

	const renderLoadModelButton = () => {
		if(Stores.HasSavedModel()) {
			return <button type="button" class="btn btn-lg btn-primary" 
						onClick={handleLoadModel}>Load Saved Model</button>
		}
	}

	return (
		<div style="padding:10px;">
		<p style="height:50px;"/>

		<div class="container">

			<div class="row align-items-start">
				<div class="col">
				<video width="420" height="420" autoPlay={true} loop={true} muted={true}>
					<source src="../../assets/animations/investing.mp4" type="video/mp4"/>
				</video>
				</div>
				<div class="col">
					<div style="font-size:32pt;"><b>incomee</b></div>
					<label style="font-size:10.5pt;font-style:italic;"><b>Incomee <i>expense</i></b> is a tool to visualize your expense flow. </label>
					<br/><br/>
					<label style="font-size:10.5pt;font-style:italic;">The final analysis will show you a funnel visualization of your expense all the way to spare cash. How much of the spending is cash back and how to pay better. It will also show a dashboard of your expense analysis with key charts to help you understand your expenses.</label>
					<br/><br/>

					<label style="font-size:10.5pt;font-style:italic;">Before we can generate the dashboard, we need some information from you. All information entered into this tool will remain in your computer, none of the information you enter will be sent to the cloud. It is completely private; you do not see a login button. To begin, click on the start modeling button below.</label>
					<p/>
					<IncomeInput store={Stores.info.net_pay} title="Monthly net pay" min_value="0" max_value="50000"
							description="Estimated monthly net pay from salary. This is monthly paycheck amount sent to you after tax. This information will be use for budgeting calculation. This information is optional but will generate better flow if filled in."/>
					<IncomeInput store={Stores.info.card_bonus} title="Annual credit card bonus" min_value="0" max_value="50000"
							description="Most credit cards offer bonus when you sign-up. Enter any card bonus received this year."/>

				</div>

			</div>
			<div class="row align-items-start">
				<div class="col">
					<h5>Why use <i>incomee expense</i> ?</h5>
					<ul class="list-group">
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Personal Financial Education</li>
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Understand of expense flow</li>
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Optimize to have more discount on your expenses</li>
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Find out some expenses that needed to be cut back</li>
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Analysis of your current flow</li>
						<li class="list-group-item" style="font-size:10pt;font-style:italic;">Totally free to use</li>

					</ul>

				</div>
				<div class="col text-center my-auto">
					<hr/>
					
					<p/>
					<button type="button" class="btn btn-lg btn-primary" onClick={handleClick}>Start Modeling</button>
					&nbsp;&nbsp;{renderLoadModelButton()}

					<p/>
					<hr/>

				</div>
			</div>
		</div>
	</div>)
}

export default Home;
